<template>

  <div class="menu-container">

    <!-- root level itens -->
    <ul class="menu">

      <li class="menu__top" style="padding-left: 55px">
        <router-link to="/" class="menu__logo" >
          <img src="../assets/amns.png" alt="icon" >
        </router-link>
      </li>

      <li>
        <a
        href="#"
        @click.prevent="updateMenu('home')"
        :class="highlightSection('home')"
        >
            <img class="inverted" src="https://image.flaticon.com/icons/svg/2/2144.svg" alt="nuke" aria-hidden="true" width="22em">
            Home
        </a>
      </li>

      <li>
        <a
        href="#"
        @click.prevent="updateMenu('ChemicalEngineering')"
        :class="highlightSection('ChemicalEngineering')"
        >
          <img class="inverted" src="https://image.flaticon.com/icons/svg/99/99026.svg" alt="nuke" aria-hidden="true" width="22em">

           Chemical Engineering
          <i class="fa fa-chevron-right menu__arrow-icon" aria-hidden="true"></i>
        </a>
      </li>

      <li>
        <a
        href="#"
        @click.prevent="updateMenu('ElectricalEngineering')"
        :class="highlightSection('ElectricalEngineering')"
        >
          <img class="inverted" src="https://image.flaticon.com/icons/svg/1835/1835740.svg" alt="nuke" aria-hidden="true" width="22em">
          Electrical Engineering
          <i class="fa fa-chevron-right menu__arrow-icon" aria-hidden="true"></i>
        </a>
      </li>

      <li>
        <a
        href="#"
        @click.prevent="updateMenu('Environmental')"
        :class="highlightSection('Environmental')"
        >
          <img class="inverted" src="https://image.flaticon.com/icons/svg/67/67788.svg" alt="rock" aria-hidden="true" width="22em">

          Environmental, Biological, Civil, Chemical Engineering
          <i class="fa fa-chevron-right menu__arrow-icon" aria-hidden="true"></i>
        </a>
      </li>

       <li>
        <a
        href="#"
        @click.prevent="updateMenu('IndustrialEngineering')"
        :class="highlightSection('IndustrialEngineering')"
        >
         <img class="inverted" src="https://image.flaticon.com/icons/svg/1726/1726768.svg" alt="rock" aria-hidden="true" width="22em">
          Industrial Engineering
          <i class="fa fa-chevron-right menu__arrow-icon" aria-hidden="true"></i>
        </a>
      </li>

       <li>
        <a
        href="#"
        @click.prevent="updateMenu('InformationTechnology')"
        :class="highlightSection('InformationTechnology')"
        >
          <img class="inverted" src="https://image.flaticon.com/icons/svg/1658/1658090.svg" alt="rock" aria-hidden="true" width="22em">
          Information Technology
          <i class="fa fa-chevron-right menu__arrow-icon" aria-hidden="true"></i>
        </a>
      </li>

      <li>
        <a
        href="#"
        @click.prevent="updateMenu('MechanicalEngineering')"
        :class="highlightSection('MechanicalEngineering')"
        >
           <img class="inverted" src="https://image.flaticon.com/icons/svg/2091/2091988.svg" alt="rock" aria-hidden="true" width="22em">
          Mechanical Engineering
          <i class="fa fa-chevron-right menu__arrow-icon" aria-hidden="true"></i>
        </a>
      </li>
    </ul>

    <!-- context menu: childs of root level itens -->
    <transition name="slide-fade">

      <div class="context-menu-container" v-show="showContextMenu">

        <ul class="context-menu">

          <li v-for="(item, index) in menuItens" :key="index">

            <h5 v-if="item.type === 'title'" class="context-menu__title">

              <i :class="item.icon" aria-hidden="true"></i>

              {{item.txt}}

              <a
              v-if="index === 0"
              @click.prevent="closeContextMenu"
              class="context-menu__btn-close"
              href="#"
              >
                <i class="fa fa-window-close" aria-hidden="true"></i>
              </a>

            </h5>

            <a
            v-else
            href="#"
            @click.prevent="openSection(item)"
            :class="subMenuClass(item.txt)"
            >
              {{item.txt}}
            </a>

          </li>

        </ul>

      </div>

    </transition>

  </div>

</template>

<script>
import menuData from './support/menu-data';
import kebabCase from 'lodash/kebabCase';

export default {
  name: 'Menu',

  data(){
    return {
      contextSection: '',

      menuItens: [],

      menuData: menuData,

      activeSubMenu: ''
    }
  },

  methods: {

    openProjectLink() {
      alert('You could open the project frontend in another tab here, so the logged admin could see changes made to the project ;)');
    },

    updateMenu(context) {
      this.contextSection = context;
      this.menuItens = this.menuData[context];

      if (context === 'home') {
        this.$router.push('/');
        window.bus.$emit('menu/closeMobileMenu');
      }
    },

    highlightSection(section) {
      return {
        'menu__link': true,
        'menu__link--active': section === this.contextSection,
      };
    },

    subMenuClass(subMenuName) {
      return {
        'context-menu__link': true,
        'context-menu__link--active': this.activeSubMenu === subMenuName,
      };
    },

    closeContextMenu() {
      this.contextSection = '';
      this.menuItens = [];
    },

    openSection(item) {
      this.activeSubMenu = item.txt;

      this.$router.push(this.getUrl(item));
      window.bus.$emit('menu/closeMobileMenu');
    },

    getUrl(item) {
      let sectionSlug = kebabCase(item.txt);

      return `${item.link}/${sectionSlug}`;
    }

  },

  computed: {
    showContextMenu() {
      return this.menuItens.length;
    },
  }

}
</script>

<style >
.inverted{
  filter: invert(1)
}


</style>
